.containerAll{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.containerGrid{
    width: 90%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    gap: .5rem;
}
.containerButtons{
    width: 90%;
    display: flex;
    justify-content: center;
    margin: 2rem;
    gap: .5rem
}
.name{
    color: black;
}
.input{
    border-radius:5px;
}
.btn{
    padding:.5rem;
    border: none;
    border-radius: 5px;
    background-color: #000;
    color: white;
}