.title{
    padding-left: .5rem;
    color: white;
    text-align: left;
    text-transform: capitalize;
}
.whiteBG{
    border-radius: 1rem;
    background-color: #fff;
    width: 95%;
    margin: auto;
}
.containerAll{
    color:  black;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    padding: .30rem;
}
.image{
    display: flex;
    justify-content: center;
    width: 100%;
    height: 400px;
}
.container{
    background-color: #fff;
    border-radius: 1rem;
    text-align: center;
    padding: .5rem
}
.containerTypes{
    margin-top: 30px;
    display: flex;
    width: 100%;
    justify-content: center;
    gap: .5rem;
}

.type{
    padding: .3rem 1rem;
    color: white;
    border-radius: 1rem;
}
.about{
    text-align: center;
}
.numbers{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
}
hr{
    margin: 0;
}
.heigthWeight{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: .5rem;
    text-align: center;
}
.heigthWeight img{
    height: 25px;
}
.id{
    text-align: center;
}



/* Progress Bar */
.statsContainer{
    display: flex;
    justify-content: space-evenly;
}

.statsInner{
    width: 30%;
    display: flex;
    justify-content: space-evenly;
}
.statsInner h3{
    font-weight: bold;
}
.containerBars{
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content:space-evenly;
}
.fatherBar{
    height: 1rem;
    width: 100%;
    border-radius: .5rem;
}
.childBar{
    height: 1rem;
    opacity: 1;
    border-radius: .5rem;
}