.containerAll{
    width: 100%;
}
.header{
    display: flex;
    justify-content: center;
    width: 100%;
    color: black;
    margin: 15px 0;
    gap: 1rem;
    align-items: center;
}
.title{
    margin: 0;
    font-size: 2rem;
    font-weight: bold;
}
.logo{
    height: 2rem
}
.nav{
    width: 100%;
    display: flex;
    text-align: center;
    justify-content: space-evenly;
    gap: 5px;
    background-color: black;
    margin:0;
    list-style: none;
    padding: 0
}
.nav a{
    text-decoration: none;
    list-style: none;
    color: white;
}
.show{
    display: none;
}