.cards{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(min(100%, 12rem), 1fr));
    grid-auto-flow: dense;
    gap: .5rem;
}
.title{
    text-align: center;
    color: #000;
    margin: 2rem;
}
.container{
    width: 90%;
    margin: auto;
    padding-bottom: .5rem;
}